<template lang='pug'>
  
  transition(name='fade')
    .loading(v-if='loadingStatus')
      img(:src='require("@/assets/img/loading_blue.svg")').loading__img

</template>

<script>
  
  export default {
    computed: {
      loadingStatus() {
        return this.$store.state.common.loading
      }
    }
  }

</script>

<style lang='sass'>

  .loading
    @include flexRow(center, center)
    position: fixed
    left: 0
    top: 0
    right: 0
    bottom: 0
    background: #fff
    z-index: 999999
    &__img
      transform: scale(.4)

  .fade-leave-active 
    transition: opacity .3s

  .fade-leave-to 
    opacity: 0
 

</style>