<template lang='pug'>
  
  .login-info(v-if='userData')
    i.fas.fa-info-circle
    p Вы вошли с аккаунта {{userData.email}}

</template>

<script>
  
  export default {
    computed: {
      userData() {
        return this.$store.state.auth.user
      }
    }
  }

</script>

<style lang='sass'>

  .login-info
    @include flexRow(center, center)
    position: fixed
    left: 20px
    bottom: 30px
    padding: 12px 15px
    background: $bluecolor
    border-radius: 12px
    font-family: $roboto
    font-weight: 400
    color: #fff
    box-shadow: 0 0x 25px rgba($bluecolor, 0.3)
    z-index: 999
    i
      margin: 0 10px 0 0

</style>