<template lang='pug'>
  
  header
    .header
      h1.header__title Todo List App
      ul().nav
        li.nav__item
          router-link(to='/').nav__link Главная страница
        li(v-if='userData').nav__item
          router-link(to='/tasks').nav__link.nav__link_tasks Список задач / личный кабинет
        li(v-if='!userData').nav__item
          router-link(to='/login').nav__link.nav__link_button Войти в аккаунт
        li(v-else).nav__item
          a(@click='signOut').nav__link.nav__link_button Выйти из аккаунта 
      


</template>

<script>
  
  export default {
    methods: {
      signOut() {
        this.$store.dispatch('signOut')
        this.$router.push('/')
        this.$toast('Вы вышли из аккаунта')
      }
    },
    computed: {
      userData() {
        return this.$store.state.auth.user
      }
    }
  }

</script>

<style lang='sass'> @import '@/assets/css/common/header' </style>