import firebase from 'firebase/app'

export default {
  state: {
    user: null,
  },
  mutations: {
    setUserData(state, payload) {
      state.user = payload
    },
    clearUserData(state) {
      state.user = null
    }
  },
  actions: {
    async userLogin({commit}, {email, password}) {
      try {
        const login = await firebase.auth().signInWithEmailAndPassword(email, password)
        commit('setUserData', login.user)
      }
      catch(e) {
        throw new Error(e)
      }
    },
    async userRegister({commit}, {email, password}) {
      try {
        const register = await firebase.auth().createUserWithEmailAndPassword(email, password)
        commit('setUserData', register.user)
      }
      catch(e) {
        throw new Error(e)
      }
    },
    async signOut({commit}) {
      await firebase.auth().signOut()
      commit('clearUserData')
    }
   },
   getters: {
     getUserData(state) {
       return state.user
     }
   }
}