import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false

import router from './router'
import store from './store'

import '@fortawesome/fontawesome-free/css/all.min.css'
import 'moment/locale/ru'

import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";

const toastConfig = {
  transition: "Vue-Toastification__fade",
  position: "bottom-right",
  timeout: 4000,
  closeOnClick: true,
  pauseOnFocusLoss: true,
  pauseOnHover: true,
  draggable: true,
  draggablePercent: 0.6,
  showCloseButtonOnHover: false,
  hideProgressBar: false,
  closeButton: "button",
  icon: true,
  rtl: false
}

Vue.use(Toast, toastConfig)

import Vuelidate from 'vuelidate'
Vue.use(Vuelidate)

import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

const firebaseConfig = {
  apiKey: "AIzaSyAGYpebLvMaau8Vl1MU9j9hAGMt_FRYJXw",
  authDomain: "todo-list-proect.firebaseapp.com",
  projectId: "todo-list-proect",
  storageBucket: "todo-list-proect.appspot.com",
  messagingSenderId: "673073364567",
  appId: "1:673073364567:web:fd8948119be4f343636d12",
  measurementId: "G-WTLKJ6VT4Y"
}

firebase.initializeApp(firebaseConfig)

firebase.auth().onAuthStateChanged(user => {
  
  if (user) {
    store.commit('setUserData', user)
    store.dispatch('loadTasks')
  }
  
  new Vue({
    router, store,
    render: h => h(App),
    mounted() {
      setTimeout(() => store.commit('setLoadingStatus', false), 500)
    }
  }).$mount('#app')
  
})



