import firebase from 'firebase/app'
import Vue from 'vue'

export default {
  state: {
    list: [
      // {title: 'Купить хлеба', desc: 'Быстро купить хлеба', time: 1616433377540, priority: 'Высокий'}
    ]
  },
  mutations: {
    pushTask(state, payload) {
      state.list.push(payload)
    },
    setTasks(state, payload) {
      state.list = payload
    },
    completeTask(state, payload) {
      let findTask = state.list.find(({key}) => key == payload)
      findTask ? Vue.set(findTask, 'completed', true) : null
    },
    deleteTask(state, payload) {
      let findTask = state.list.findIndex(({key}) => key == payload)
      state.list.splice(findTask, 1)
    }
  },
  actions: {  
    async completeTask({commit}, key) {
      try {
        await firebase.database().ref('tasks').child(key).update({
          completed: true
        })

        commit('completeTask', key)

      }
      catch(e) {
        throw new Error(e)
      }
    },
    async deleteTask({commit}, key) {
      try {
        await firebase.database().ref('tasks').child(key).remove()

        commit('deleteTask', key)

      }
      catch(e) {
        throw new Error(e)
      }
    },
    async createTask({getters, commit}, task) {
      const newTask = { ...task, time: new Date().getTime(), user: getters.getUserData.uid }

      try {
        const dbTask = await firebase.database().ref('tasks').push(newTask)
        commit('pushTask', { ...newTask, key: dbTask.key })
        
      }
      catch(e) {
        throw new Error(e)
      }
    },
    async loadTasks({getters, commit}) {
      try {
        const tasks     = await firebase.database().ref('tasks').once('value'),
              copyTasks = {...tasks.val()}

        Object.keys(tasks.val()).map(key => copyTasks[key].key = key)
        const filteredTasks = Object.values(copyTasks).filter(({user}) => user == getters.getUserData.uid)

        commit('setTasks', filteredTasks)
      } 
      catch(e) {
        console.log(e)
        throw e
      }
    }
  }
}